<template>
  <div class="category-items">
    <ul @click.prevent="dblclck()">
      <li v-for="(item, i) in items" :key="i">
        <div
          class="category-items__item"
          @click.prevent="goToDrinkCard(item.idDrink)"
        >
          <div class="image-overlay item-image">
            <img :src="item.strDrinkThumb" height="150" alt="drink" />
          </div>
          <div class="text-block">
            <h4 class="title">{{ item.strDrink }}</h4>
            <!-- <router-link class="card-link" to="/drink-card">Learn more</router-link> -->
            <p>learn more...</p>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  data() {
    return {
      itemsList: null,
    };
  },
  methods: {
    async goToDrinkCard(id) {
      await this.$store.dispatch("getDrinkCard", id);
      await this.$router.push("/drink-card").catch(() => {});
    },
    async dblclck() {
      await event.target.click();
    },
  },
  computed: {
    items() {
      return this.$store.getters.getPickedList
        ? this.$store.getters.getPickedList.drinks
        : [];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../scss/mixins.scss";
@import "../scss/fonts.scss";
.category-items {
  // max-height: calc(100vh-90px);
  width: 100%;
  padding: 15px 20px 5px;
  display: grid;
  overflow: auto;
  ul {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    gap: 15px;
    li {
      color: orange;
      font-size: 20px;
      .category-items__item {
        display: flex;
        // align-items: stretch;
        align-content: stretch;
        // box-shadow: 0 0 7px #fff, 0 0 10px $base-color, 0 0 11px #fff,
        //   0 0 12px $base-color;
        @include neon-border($base-color);
        border-radius: 5px;
        overflow: hidden;
        cursor: pointer;
        .item-image {
          position: relative;
        }
        .image-overlay::after {
          content: "";
          display: block;
          position: absolute;
          top: 0px;
          left: 0px;
          width: 100%;
          height: 100%;
          background: rgba(240, 43, 224, 0.096);
        }
        .text-block {
          padding: 20px 15px;
          flex: 1 1 auto;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          .title {
            @include card-item-title-font($base-color);
            align-self: flex-start;
          }
          p {
            font-family: "La Belle Aurore";
            // color: fuchsia;
            @include card-item-link-font($card-item-link-color);
            font-size: 26px;
            align-self: flex-end;
          }
        }
      }
    }
  }
}
</style>